.preview {
    background-color: var(--main);
    padding: 20px;
    display: flex;
    width: 80%;
    border-radius: 18px;
    justify-content: space-between;
    color: var(--accent) !important;
}

.link {
    display: flex;
    width: 80%;
    font-size: 1.6rem;
    border-radius: 18px;
    justify-content: space-between;
    cursor: pointer;
    text-decoration: none;
}

.info {
    display: flex;
    align-items: center;
}

.ban > * {
    color: var(--negative);
}

.unban > * {
    color: var(--positive);
}

.button {
    cursor: pointer;
    min-width: 20px;
}

.button:hover > * {
    color: var(--accent);
}

.banned .button:hover > * {
    color: var(--main);
}

.banned {
    background-color: var(--banned);
}

@media screen and (max-width: 1200px) {
    .info {
        font-size: 1.3rem !important;
    }
}

@media screen and (max-width: 500px) {
    .info,
    .info * {
        font-size: 1rem;
    }
}

@media screen and (max-width: 350px) {
    .preview {
        padding: 10px;
    }
    .info,
    .info * {
        font-size: 0.8rem;
    }
}
