* {
    --main: #2e2f47;
    --banned: #64656c;
    --negative: #e34a4a;
    --positive: #71a842;
    --accent: #f5f5f5;
    --secondary: #d1dee4;
    --secondary-light: #b9d6e4;
    color: #f5f5f5;
    font-family: "Roboto", sans-serif !important;
    box-sizing: border-box;
    border: none;
    padding: 0;
    margin: 0;
    border: none;
    text-decoration: none;
}
button {
    background: none;
}

html {
    font-size: 10px;
}

.app {
    width: 1440px;
    background-color: var(--secondary);
    display: flex;
    height: 100%;
    min-height: 100vh;
}

.wrapper {
    width: 100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.content {
    width: 100%;
    box-sizing: border-box;
    height: 100%;
    padding: 40px 0;
    background-color: var(--secondary);
    min-height: 100vh;
    position: relative;
}

.banned {
    animation: banned 2s forwards;
    opacity: 0.5;
}

.unbanned {
    animation: unbanned 2s forwards;
    opacity: 0.5;
}

.negative,
.trash,
.remove,
.ban,
.reject {
    fill: var(--negative);
    color: var(--negative);
}
.negative,
.trash,
.remove,
.ban {
    cursor: pointer;
}

.positive,
.confirm,
.unban {
    fill: var(--positive);
    color: var(--positive);
}

.modal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    z-index: 10000;
}

.accordion:hover {
    background-color: var(--secondary-light);
}

.highlight {
    background-color: lightgoldenrodyellow;
    color: var(--main);
    font-weight: bold;
    padding: 2px;
    border-radius: 5px;
}

@media screen and (max-width: 776px) {
    .content {
        padding-top: 15px;
    }
}
